.footer-copyright {
  background-color: #282c35;
  padding-top: 3px;
  padding-bottom: 3px;
}

.footer-copyright p {
  margin: 10px;
  color: #ccc;
}

.my-footer {
  padding-top: 30px;
  background-color: #2c3e50;
  color: white;
}

.my-footer ul {
  list-style-type: none;
  padding-left: 0;
}

.my-footer a:hover,
.my-footer a:focus {
  text-decoration: none;
  color: white;
}

.social-network {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.social-network a {
  font-size: 32px;
  margin-right: 5px;
  color: #f9f9f9;
  padding: 10px;
  transition: 0.2s;
}

.social-network a:hover {
  text-decoration: none;
}

.social-network .facebook:hover {
  color: #0077e2;
}

.social-network .google:hover {
  color: #ef1a1a;
}

.social-network .twitter:hover {
  color: #00aced;
}
